.app {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.board {
  display: flex;
  flex-wrap: wrap;
  width: calc(64px * 8);
  height: calc(64px * 8);
}

.cell {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 64px;
  height: 64px;
}

.cell img {
  position: relative;
  width: 48px;
  height: 48px;
}

.black {
  background-color: #e7ecd7;
}

.white {
  background-color: #0aa384;
}

.selected {
  background-color: #e5e33a;
}

.available {
  width: 12px;
  height: 12px;
  border-radius: 12px;
  background-color: #fdf908;
}

.enemy {
  background-color: #fdf908;
}

.lost {
  height: calc(50vh - 65px);
  padding: 30px;
  margin-left: 80px;
  background-color: #e7ecd7;
}

.lost img {
  width: 20px;
  height: 20px;
}

.timer {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-right: 80px;
}

.timer > * {
  margin: 0;
}

.timer button {
  width: 100%;
  padding: 6px 12px;
  border: none;
  border-radius: 10px;
  font-size: 20px;
  color: #ffffff;
  background-color: #0aa384;
  cursor: pointer;
}
